import React, { useState } from 'react';
import { NavbarBase, Item } from './styled';
const Component = ({ items }) => {
    const [optionSelected, setOptionSelected] = useState(0)
    const renderItems = items.map((item, index) => <Item key={`${index}-item`} isSelect={item.value == optionSelected} onClick={() => setOptionSelected(item.value)}>{item.name}</Item>)

    return (
        <NavbarBase>
            {renderItems}
        </NavbarBase>
    );
}

export default Component;
export { Component as NavbarBase }